import React, { Component } from 'react';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Todo from './components/Todo';
import NewTodo from './components/NewTodo';

class App extends Component {

  //State
  state = {
    todos: [],
    colors: ['white', 'pink','gray', 'green', 'yellow', 'red','blue', 'purple']
  };

  //Remove the Todo Item
  removeItem = e => {

    let id = e.target.id;
    let newTodos = this.state.todos.slice(0);

    for (let todo of newTodos){
      
      if (todo.id.toString() === id.toString()){
        newTodos.splice(newTodos.indexOf(todo), 1);
        break;
      }
    }
    this.setState({todos:newTodos});
  }

  //Create a new Todo Item
  newItem = color => {
    let newTodos = this.state.todos.slice(0);
    newTodos.push({
      id: this.state.todos.length,
      color
    });
    this.setState({todos:newTodos});
  }

  //Main function - Render everything
  render() {
    return (
      <div className="container-fluid">
          {/* Loop through all colors, add a button for each */}
        <div className="d-flex justify-content-center">
          {this.state.colors.map(color => {return <NewTodo action={() => {this.newItem(color)}} color={color}/>})}
        </div>
          {/* Loop through all Todos, and display them */}
        <div className="d-flex mt-4">
          {this.state.todos.map(todo => <Todo color={todo.color} key={todo.id} index={todo.id} removeItem={this.removeItem}/>)}
        </div>
      </div>
    )
  }
}
export default App;